
.bodyChart {
    display: flex;
    flex-direction: column;

    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    align-items: center;
    color: white;
}
.bodyChart .chart {
    width: 100%;
    min-height: 74%;
}
.bodyChart p{
    font-size: 12px;
}

.bodyChart .legenda {
    display: flex;
    justify-content: space-around;
    align-items: center;
    
    width: 100%;
    margin: 0px;
    padding: 0px;
}
.bodyChart .legenda span {
    font-size: 10px;
    margin: 0px;
    padding: 0px;
}

/*Obrigar Graficos de Histograma no Tamanho Correto*/
.rv-xy-plot {
    height: 100% !important;
}