

.MODAL {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;

    z-index: 9999;
    background-color: rgba(0,0,0,0.80);
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modalConteiner {
    background-color: #fff;
    color: #000;
    width: 60%;
    height: 60%;

    border-radius: 20px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.modalClose {
    background-color: transparent;
    border: none;
    outline: none;
    width: 32px;
    height: 32px;

    display: flex;
    position: relative;
    right: calc(-50% + 16px);
    top: calc(0);
    cursor: pointer;
}
/*Pseudo Selector*/
.modalClose::before {
    content: " ";
    position: absolute;
    width: 2.5px;
    height: 24px;
    background-color: #000;
}
.modalClose::after{
    content: " ";
    position: absolute;
    width: 2.5px;
    height: 24px;
    background-color: #000;
}
.modalClose::before{
    transform: rotate(45deg);
}
.modalClose::after{
    transform: rotate(-45deg);
}
