.dropdownList{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 300px !important; 
    max-height: 300px !important; 
    
    margin: 0;
    padding: 0;
    
}


.dropdownList .dropdown-menu {
    height: 300px !important; 
    max-height: 300px !important; 

    overflow: auto;
}

.dropdownList .dropdown-menu::-webkit-scrollbar {
    width: 10px;
    height: 3px; 
}

.dropdownList .dropdown-menu::-webkit-scrollbar-track {
    background: #333;/*transparent;*/
    padding: 2px;
}

.dropdownList .dropdown-menu::-webkit-scrollbar-thumb {
    background-color: #000;
}

