
.bodyTable {
    margin: 0 !important;
    padding: 5px !important;
    width: 100%;
    height: 100%;
}
.bodyTable h2 {
    height: 10%;
}
.bodyTable table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
    height: 90%;
    max-height: 100%;
}
thead {
    width: calc( 100% - 10px );/* scrollbar  de 10px definido abaixo */
}
thead, tbody tr {
    display: table;
    table-layout:fixed;/* even columns width , fix width of table too*/
}
tbody tr {
    width: 100%;
}
tbody {
    display: block;
    overflow:scroll;
    height: 85%;
}
td, th {
    border: 1px solid #dddddd;
    text-align: center;
    padding: 0.1px;
    
}
  
tr:nth-child(even) {
    background-color: #445;
}


tbody::-webkit-scrollbar {
    width: 10px;
    height: 3px; 
}

tbody::-webkit-scrollbar-track {
background: transparent;
padding: 2px;
}

tbody::-webkit-scrollbar-thumb {
background-color: #000;
}