
.MenuBarMAP:first-child {
    display:flex;
    height: 100%;

    justify-content: right;
    align-items: center;
    
}
.MenuBarMAPItem {
    /*width: 100%;*/
    height: 100%;
    margin: 2px;
    justify-content: center;
    align-items: center;
    
}
.MenuBarMAPItem svg {
    height: 100%;
}

.btn-secondary {
    background-color: transparent;
    border: none;
}

.dropdown-toggle {
    color: white !important;
}
.dropdown-toggle:hover, 
.btn-secondary:hover,
.MenuBarMAPItem svg:hover {
    color: orange !important;
    background-color:rgb(36, 36, 36);
}

.labelDropDown:hover{
    color: orange !important;
}


/* STYLE OF FILTER PAINEL*/
.FilterConteiner{
    display: flex;
    flex-direction: column;
}
.row_Filter_Conteiner {
    display: flex;
    width: 100%;
    margin: 2px;
    padding: 3px;
    justify-content: space-between;
}
.inputNumber {
    /*font-size:1em;*/
    /*width:2.5em;*/
    padding:1px;
    margin:0;
    border-radius:2px; 
    border: 1px solid #000;
    text-align:center;
}
.button_FilterSucess,
.button_FilterFalha {
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
  }
  .button_FilterFalha {
    background-color: red;
  }
@media screen and (max-width: 600px) {
    .row_Filter_Conteiner {
      flex-direction: column;
    }
  }