
/* CSS CONFIGURACAO DO TOP */

.miniBoddy {
    display: flex;
    width: 100%;
    height: 100%;
    color: white;
    align-items: center;
    justify-content: space-around;
    font-size: 12px;
}

.flex-column {
    display: flex;
    flex-direction: column !important;
}
.flex-row {
    display: flex;
    flex-direction: row !important;
    white-space: nowrap;
}
.inline {
    display: inline;
    white-space: nowrap;
}
.miniBoddy .Parte1{
    width: 60%;
    flex-direction: column;
}

.miniBoddy .Parte2{
    width: 40%;
}

.tituloSETUP {
    width: 100%;
    padding: 10;
    margin: 10;
    justify-content: center;
}
.tituloSETUP h1{
    color: white;
    width: 100%;
    
}
.bodySETUP {
    display: flex;
    flex-direction: row;
    width: 100%;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    color: white;
}

.bodySETUP .parte{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.bodySETUPComponent{
    display: flex;
    flex-direction: column;
    margin: 10px;
    padding: auto;
    
}



/* CSS CONFIGURACAO DO LATERAL */

.Configslateral{
    color: white;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}
.Configslateral .tituloSETUP{
    font-size: 12px;
}

.Configslateral .bodySETUP{
    display: flex;
    flex-direction: column;
}


#thumbwrap {
	position:relative;
    
}
a.thumb {
    color: white;
}
a.thumb:link { text-decoration: none; }
a.thumb:visited { text-decoration: none; }
a.thumb:hover { text-decoration: none; }
a.thumb:active { text-decoration: none; }

.thumb span { 
	position:absolute;
	visibility:hidden;
    width: 400%;
    height: 400%;
}
/*.thumb:hover, */
.thumb:hover span { 
	visibility:visible;
	top:0;
	left:100px; 
	z-index:1;
}



.Configslateral .Parte1{
    /*height: 50%;*/
    max-height: 50%;
    padding-bottom: 20px;
}
.Configslateral .Parte2{
    height: 50%;
    height: 50%;
    max-height: 50%;
    padding-bottom: 25px;
}

.Configslateral .Parte1 .title {
    display: flex;
    flex-direction: row;
}

.Configslateral .Parte1 .Lista {
    display: inline-block;
    height: 100%;
    width: 100%;
    padding: 10px;
    margin: 0;
    overflow: auto;
}


.Configslateral .Parte2 .Lista {
    display: inline-block;
    height: 100%;
    width: 100%;
    padding: 10px;
    margin: 0;
    overflow: auto;
}

/* Customizar Barra Lateral */
.Configslateral .Parte1 .Lista::-webkit-scrollbar {
    width: 10px;
    height: 3px; 
  }
  
  .Configslateral .Parte1 .Lista::-webkit-scrollbar-track {
    background: transparent;
    padding: 2px;
  }
  
  .Configslateral .Parte1 .Lista::-webkit-scrollbar-thumb {
    background-color: #000;
  }


  .Configslateral .Parte1 .Lista {
