
/* CSS CONFIGURACAO DO TOP */

.VRPminiBoddy {
    display: flex;
    width: 100%;
    height: 100%;
    font-size: 12px;
    color: white;
    align-items: center;
    justify-content: space-around;
}


.VRPminiBoddy .Parte1{
    width: 60%;
    flex-direction: column;
}

.VRPminiBoddy .Parte2{
    width: 40%;
    height: 100%;
    align-items: center;
    justify-content: center; /*cetraliza verticalmente*/
}

.VRPtituloSETUP {
    width: 100%;
    padding: 10;
    margin: 10;
    justify-content: center;
}
.VRPtituloSETUP h1{
    color: white;
    width: 100%;
    
}
.VRPbodySETUP {
    width: 100%;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    color: white;
}

.VRPbodySETUP .parte{
    display: flex;
    flex-direction: row;
    
    width: 100%;
}

.parte{
    display: flex;
    flex-direction: row;
    
    width: 100%;
}

.VRPbodySETUPComponent{
    display: flex;
    height: 100%;
    flex-direction: column;
    margin: 0px;
    padding: 0;
    width: 100%;
    font-size: 10px !important;
    color: white;
}



/* CSS CONFIGURACAO DO LATERAL */

.VRPConfigslateral{
    color: white;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 10px !important;
}

.VRPConfigslateral .Parte1{
    height: 32%;
}
