.leaflet-container{
    width: 100%;
    height: 100%;
    z-index: 0;
}

/* CSS QUE IMPEDE FICAR DUPLICANDO CONTROLES */

.info_legend ~ .distrito ~ .leaflet-control ~ .leaflet-control{
    display: none;
}
.info_legend.customers ~ .leaflet-control ~ .leaflet-control{
    display: none;
}
.info_legend.customers:last-of-type ~ .leaflet-control ~ .leaflet-control{
    display: auto;
}
